import React, { Component } from 'react'
import Header from '../../components/Header'
import Topic from '../../components/TopicItem/topicItem'
import Aside from '../../components/Aside/index'
import {searchTopic} from '../../servers/index'
import {message} from 'antd'

export default class Search extends Component {
    constructor(){
        super()
        this.state = {
            list: [],
            page: 1,
            has_next: false,
            isDataArrive: true 
        }
    }
    componentDidMount(){
        const page = this.state.page
        const page_size = 10
        const page_num = page
        this.getSearchTopic(page_num,page_size)
        window.addEventListener('scroll', this.loadMore.bind(this));
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.loadMore.bind(this))
    }
    loadMore() {
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
        let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
        if (scrollTop + clientHeight === scrollHeight) {
            console.log("到底部了")
            this.getMoreSearchTopict()
        }
    }

    getSearchTopic(page_num, page_size){
        const key = this.props.location.search.split('=')[1]
        const data = {
            key,page_num,page_size
        }
        searchTopic(data).then(res=>{
            if(res.code===200){
                const { has_next, next_number } = res.data.pageInfo
                this.setState({ list: res.data.topics, has_next, page: next_number })
            }
        })
    }

    getMoreSearchTopict() {
        const { isDataArrive, has_next, page } = this.state
        if (!isDataArrive || !has_next || page === 1) {
            return
        }
        this.setState({ isDataArrive: false })
        const key = this.props.location.search.split('=')[1]
        const data = {
            key,
            page_num: page,
            page_size: 10,
            
        }
        
        searchTopic(data).then(res => {
            if (res.code === 200) {
                const { has_next, next_number } = res.data.pageInfo
                this.setState({
                    list: [...this.state.list, ...res.data.topics],
                    has_next, page: next_number, isDataArrive: true
                })
            } else {
                message.error(res.error)
            }
        })

    }


    render() {
        const {list} = this.state
        return (
            <div>
                <Header {...this.props} />
                <img src={require('../../assets/imgs/page_bg1.png')} className="figure-img img-fluid w-100" alt="" />
                <div className="container">
                    <div className="row">
                        <div className="col-md-8">
                            {list.map((item, index) => {
                                return (
                                    <Topic data={item} key={index} {...this.props} />
                                )
                            })}

                            {list.length === 0 ?
                                <div className="no-data">未找到相关内容</div> : null
                            }

                        </div>
                        <div className="col-md-4">
                            <Aside />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}