import React, { Component } from 'react'
// import { Link } from 'react-router-dom'
import './index.css'

function entityToString(entity) {
    var div = document.createElement('div');
    div.innerHTML = entity;
    var res = div.innerText || div.textContent;
    return res;
}
export default class TopicItem extends Component {

    render() {
        const data = this.props.data
        const images = data.images.map(img => {
            return entityToString(img)
        })
        return (
            <div className="card" style={{ marginBottom: '30px' }}>
                <div className="card-body">
                    <div className="topic-item-title" onClick={() => this.props.history.push(`/articleDetail/${data.username}/${data.id}`)}>
                        {data.title}
                    </div>
                    {images.length > 2 ?
                        <div className="row" style={{marginBottom:20,marginTop:20}}>
                             <div className="col-md-4" style={{padding:0,}}>
                                <img src={images[0]} alt="" className="topic-img-1" />
                            </div> 
                            <div className="col-md-4" style={{paddingLeft:10,paddingRight:10}}>
                                <img src={images[1]} alt="" className="topic-img-1" />
                            </div> 
                            <div className="col-md-4" style={{padding:0,}}>
                                <img src={images[2]} alt="" className="topic-img-1" />
                            </div> 
                        </div> : null
                    }
                    <div className="row">
                        {images.length > 0 && images.length < 3 ?
                            <div className="col-md-4">
                                <img src={images[0]} alt="" className="topic-img" />
                            </div> : null
                        }

                        <div className={images.length > 0 ? "col-md-8" : "col-md-12"}>
                            <div className="card-text card-topic-text">
                                {data.introduce}
                            </div>
                            <p className="card-text" style={{ marginTop: '20px' }}>
                                <small className="text-muted">
                                    <span>{data.author}</span>
                                    <span>{data.created_time}</span>
                                    <span>已阅读:{data.browse_count}</span>
                                    <span>点赞:{data.heart_count}</span>
                                </small>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}